@use "./breakpoints" as *;
@use "./colors" as *;
@use "./buttons" as *;
@use "./sectionmarkers" as *;
@use "./cards" as *;

@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  border: 0;
}

html,
body,
div,
span,
object,
iframe,
figure,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
code,
em,
img,
small,
strike,
strong,
sub,
sup,
tt,
b,
u,
i,
ol,
ul,
li,
fieldset,
form,
label,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
main,
canvas,
embed,
footer,
header,
nav,
section,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

body {
  background: $white;
  color: $gray;
  line-height: 1;
  font-family: "Red Hat Display", sans-serif;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 1.625rem;
  overflow-x: hidden;
}

header {
  overflow: hidden;

  nav {
    #logo {
      padding: 3rem 0 0 0;
      text-align: center;
    }

    .image-container {
      width: 111%;
      margin: 0 0 0 -1.35rem;
      padding: 0;

      @include md {
        width: 106.5%;
        margin: 0 0 0 -1.56rem;

        img {
          width: 100%;
        }
      }

      img {
        width: 100%;
        height: 100%;
        padding: 3.375rem 0 3rem 0;
      }
    }

    // .images-container {
    //   display: flex;
    //   justify-content: space-between;
    //   overflow: hidden;
    //   width: 100%;
    //   padding: 3.375rem 0 3rem 0;

    //   div:nth-child(1) {
    //     object-fit: cover;
    //     margin: 0 0 0 -1.213rem;
    //     padding: 0;

    //     img {
    //       width: 12.438rem;
    //     }
    //   }

    //   div:nth-child(2) {
    //     object-fit: cover;
    //     margin: 0 -2.313rem 0 0;
    //     padding: 0;

    //     img {
    //       width: 12.438rem;
    //       margin: 0 1.2rem 0 0;
    //     }
    //   }
    // }

    div {
      padding: 0 1.5rem;

      h1 {
        font-size: 2.5rem;
        font-weight: 900;
        line-height: 2.75rem;
        text-align: center;
        color: $dark-blue;
        padding: 0 1.5rem;

        @include md {
          padding: 0 14rem;
        }
      }

      p {
        font-size: 1rem;
        line-height: 1.625rem;
        text-align: center;
        color: $gray;
        padding: 1.5rem 0 2rem 0;

        @include md {
          padding: 1.5rem 9rem 2rem;
        }
      }
    }

    .cta-container {
      display: flex;
      flex-direction: column;
      text-align: center;
      align-items: center;

      @include md {
        flex-direction: row;
        justify-content: center;
        gap: 1rem;
      }

      button {
        @include btn-style();
      }

      button:nth-child(1) {
        width: 12.063rem;
        color: $white;
        background: $teal;

        @include md {
          margin: 0;
        }

        span {
          color: $light-blue;
        }
      }

      button:nth-child(2) {
        width: 8.688rem;
        color: $white;
        background: $dark-purple;
        margin: 0 0 4rem 0;

        @include md {
          margin: 0;
        }
      }
    }
    #section-marker-1 {
      @include section-marker;

      @include md {
        padding: 5rem 0 4rem 0;
      }
    }
  }
}

main {
  margin: 0 auto;

  .flex-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
    padding: 1.5rem;

    @include md {
      flex-direction: row;
      padding: 1.5rem 2.5rem 2.25rem 2.438rem;
    }

    .card {
      display: flex;
      gap: 1.563rem;

      @include md {
        gap: 0.688rem;
      }

      img {
        width: 100%;
        height: 8.938rem;
        max-height: 100%;
        border-radius: 0.5rem;
        object-fit: cover;

        @include md {
          height: 9.75rem;
          max-height: 100%;
        }
      }
    }

    // .top-cards {
    //   display: flex;
    //   gap: 1.563rem;

    //   img {
    //     width: 100%;
    //     border-radius: 0.75rem;
    //   }
    // }

    // .bottom-cards {
    //   display: flex;
    //   gap: 1.563rem;

    //   img {
    //     width: 100%;
    //     border-radius: 0.75rem;
    //   }
    // }
  }

  #image-text {
    @include card-text;

    p:nth-child(1) {
      color: $teal;
      font-size: 1rem;
      line-height: 1.625rem;
      font-weight: 900;
      letter-spacing: 0.25rem;
      text-transform: uppercase;
    }

    p:nth-child(2) {
      color: $dark-blue;
      font-size: 2rem;
      line-height: 2.25rem;
      font-weight: 900;
      padding: 1.5rem 0 2rem;

      @include md {
        font-size: 2.5rem;
        line-height: 2.25rem;
        padding: 1.25rem 9rem 2rem;
      }
    }

    p:nth-child(3) {
      color: $gray;
      font-size: 1rem;
      line-height: 1.625rem;
      font-weight: 500;
      padding: 0 0 4rem;

      @include md {
        padding: 0 4rem 5rem;
      }
    }
  }
  #section-marker-2 {
    @include section-marker;
    margin: 0 0 -5.75rem 0;
    z-index: 10;
  }
}

footer {
  background: linear-gradient(
      to top,
      rgba(77, 150, 169, 0.9),
      rgba(77, 150, 169, 0.9)
    ),
    url("../assets/mobile/image-footer.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  #footer-text {
    @include card-text;
    color: $white;
    padding: 5.75rem 1.5rem 0;

    p:nth-child(1) {
      font-size: 2rem;
      line-height: 2.25rem;
      font-weight: 900;
      text-align: center;
    }

    p:nth-child(2) {
      font-size: 1.125rem;
      line-height: 1.625rem;
      font-weight: 500;
      text-align: center;
      padding: 1.5rem 0 2rem;
    }

    button {
      @include btn-style;
      width: 12.063rem;
      color: $white;
      background: $dark-purple;
      margin: 0 0 4.5rem;

      span {
        color: $light-purple;
      }
    }
  }
}
